:global(.styledForm) {
  display: flex;
  flex-direction: column;
  padding: 16px;
  width: 100%;
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

:global(.styledInput) {
  color: #171718;
  font: inherit;
  font-size: 1.2rem;
  padding: 16px;
  border-radius: 4px;
  border: 1px solid #bdbdbd;
  width: 100%;
  outline: 0;
}

:global(.styledInput:focus) {
  border: 1px solid #2b32b2;
}

:global(.labelStyles) {
  display: inline-block;
  font-size: 20px;
  color: #171718;
  cursor: pointer;
}

:global(.labelStyles:nth-child(2)) {
  padding-top: 30px;
}

:global(.styledButton) {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: #3f51b5;
  border: 0;
  outline: 0;
  margin-top: 15px;
  padding: 8px 24px;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.05);
  font: inherit;
  text-transform: uppercase;
  user-select: none;
  transition: background-color 200ms ease-in-out;
}

:global(.styledButton:hover) {
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.15);
  background-color: #303f9f;
}

:global(table .styledButton) {
  margin-top: 0px;
}
